:root {
  --color-white: #fff;
  --color-accent-yellow: #f3c653;
  --font-primary: "Montserrat", sans-serif;
  --font-secondary: "Times New Roman", serif;
  --font-logo: "Film P3", sans-serif;
  --animation: .5s cubic-bezier(.19, 1, .22, 1);
  --padding-mobile: 15px;
  --padding-tablet: 45px;
  --padding-desktop: 85px;
}

.debug:before {
  content: "Small";
  letter-spacing: .05rem;
  color: #fff;
  background: #000;
  padding: 1rem 2rem;
  font-family: sans-serif;
  font-weight: 700;
  position: fixed;
  bottom: 0;
  right: 0;
}

@media (min-width: 1024px) {
  .debug:before {
    content: "medium";
  }
}

@media (min-width: 1440px) {
  .debug:before {
    content: "large";
  }
}

@media (min-width: 1920px) {
  .debug:before {
    content: "xlarge";
  }
}

.address, .logo, .header__tel, .header__tel-link, .header__link {
  color: inherit;
  text-decoration: none;
}

.swiper-wrapper, .socials, .header__tel-list, .header__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

*, :after, :before {
